
import XDialog from 'vux/src/components/x-dialog/index.vue'

import Msg from '../components/Main/Msg'
import MsgImg from '../components/Main/MsgImg'
import MsgOnlyImg from '../components/Main/Img'
import BpMsg from '../components/Main/BpMsg'
import DsMsg from '../components/Main/DsMsg'
import BpWindow from '../components/Main/BpWindow'
import DsWindow from '../components/Main/DsWindow'

export default {
data () {
return {
chatlist: [{
content: '~',
likes: 666,
type: 3
}, {
content: '~',
likes: 688,
type: 4
}, {
content: '',
likes: 5,
type: 0
}, {
content: '',
likes: 15,
type: 1
}, {
content: '',
likes: 666,
type: 2
}, {
content: '',
likes: 666,
type: 0
}],
bpWindowVisible: false,
dsindowVisible: false,
textImgVisible: false
}
},
mounted () {
},
methods: {
like (index) {
this.chatlist[index].likes++
},
openDialog () {
this.$bpDialog({
title: '',
content: '<b></b>',
stopAuto: false,
confirmCallback: () => {
setTimeout(() => {
this.$bpDialog.hide()
}, 3000)
}
})
}
},
components: {
Msg,
MsgImg,
MsgOnlyImg,
BpMsg,
DsMsg,
BpWindow,
DsWindow,
XDialog
}
}



